import { useState } from 'react'
import Doctor from '../../Doctor/Doctor'
import DropDounSerch from '../../DropDounSerch/DropDounSerch'
import { ReactComponent as Serch } from '../../../icons/search.svg';
import './mainPageFindDoctor.css'
import { routeSpecialist, routeUser } from '../../../http/routeBack'
import { InputNumberWithText } from '../../InputNumberWithText/InputNumberWithText';
import { useListUsers } from '../../../hooks/useListUsers';
import { Pagination } from '../../AdminPanel/ComponentsAdminTools/Pagination/Pagination';

function MainPageFindDoctor() {
	const [selectSpecialiti, setSelectSpecialiti] = useState({})
	const [fromPrice, setStateFromPrice] = useState('')
	const [toPrice, setStateToPrice] = useState('')
	const [fromExperience, setFromExperience] = useState('')
	const [toExperience, setToExperience] = useState('')
	let limit = 2
	if (window.innerWidth < 450) {
		limit = 5
	}
	const {
		goToPage,
		goToNextPage,
		goToPrevPage,
		maxPage,
		activePage,
		arrayUser } = useListUsers(
			`${routeUser.serchDoctor}?`, 'doctor',
			`&specialist=${selectSpecialiti._id ? selectSpecialiti._id : ''}
			&limit=${limit}
			&fromExperience=${fromExperience ? fromExperience : ''}
			&toExperience=${toExperience ? toExperience : ''}
			&fromPrice=${fromPrice ? fromPrice : ''}
			&toPrice=${toPrice ? toPrice : ''}`,
		)
	return (
		<section className={'MainPageFindDoctor'}>
			<h2 className={'MainPageFindDoctor__title'}>Знайти Лікаря</h2>
			<div className={'MainPageFindDoctor__wrapperSerch'}>
				<div className={'MainPageFindDoctor__leftBlock'}>
					<span>Виберіть лікаря</span>
					<div className={'MainPageFindDoctor__dropDounWrapper'}>
						<DropDounSerch
							selectData={selectSpecialiti}
							setSelectData={setSelectSpecialiti}
							rout={routeSpecialist?.getAllSpecialist}
							species={'specialist'}
							svg={<Serch />}
						>
							Спеціальність
						</DropDounSerch>
						<div>
							<div className={'MainPageFindDoctor__titlePrice'}>За ціною консультації</div>
							<div className={'MainPageFindDoctor__wrapperPrice'}>
								<InputNumberWithText value={fromPrice} setValue={setStateFromPrice} placeholder={50} inputTitle={'вiд'} text={'грн'} />
								<InputNumberWithText value={toPrice} setValue={setStateToPrice} placeholder={500} inputTitle={'до'} text={'грн'} />
							</div>
						</div>
						<div>
							<div className={'MainPageFindDoctor__titlePrice'}>За Досвідом</div>
							<div className={'MainPageFindDoctor__wrapperPrice'}>
								<InputNumberWithText value={fromExperience} setValue={setFromExperience} placeholder={5} inputTitle={'вiд'} text={'років'} />
								<InputNumberWithText value={toExperience} setValue={setToExperience} placeholder={15} inputTitle={'до'} text={'років'} />
							</div>
						</div>
					</div>
				</div>
				<div className={'MainPageFindDoctor__wrapperShow'}>
					<div className={'MainPageFindDoctor__wrapperShowDoctor'}>
						{arrayUser.length ? arrayUser.map(item =>
							<Doctor
								price={item.consultationPrice}
								city={item.city}
								country={item.country}
								region={item.region}
								id={item.id}
								name={item.name}
								specialty={item.mainSpecialist}
								key={item.id}
								item={item} />)
							:
							<div className={'MainPageFindDoctor__notData'}>Немає таких фахівців</div>
						}
					</div>
					<Pagination
						goToPrevPage={goToPrevPage}
						goToNextPage={goToNextPage}
						goToPage={goToPage}
						maxPage={maxPage}
						activePage={activePage} />
				</div>
			</div>
		</section>
	)

}

export default MainPageFindDoctor